import React, {Component} from 'react';
import {getStrFromDate, aDay} from '../handleDate';

export class NewTask extends Component {
  dateInArr () {
    let arrDays = [];
    let count = 0;
    for (let i = 0; i < 365; i++) {
      let day = getStrFromDate(new Date(Date.now() + count));
      count += aDay;
      arrDays.push(<option key={i} value={day}>{day}</option>);
    }
    return arrDays;
  }
  click = (e) => {
    e.preventDefault();
    let form = document.forms[0];
    if (form.children[0].value.trim() !== ``) {
      let obj = {};
      obj.done = false;
      obj.key = `${Date.now()}`;
      obj.title = form.children[0].value;
      form.children[0].value = ``;
      [...form.elements.priority.options].forEach(op => {
        if (op.selected) obj.priority = op.value;
      });
      [...form.elements.priority.options][0].selected = true;
      [...form.elements.date.options].forEach(op => {
        if (op.selected) obj.date = op.value;
      });
      [...form.elements.date.options][0].selected = true;
      obj.description = form.elements.description.value;
      form.elements.description.value = ``;
      this.props.add(obj);
    }
  };
  render () {
    return (
      <form id="add" name="add">
        <input name="title "type="text" placeholder="Title"/>
        <select name="priority" onChange={_ => false}>
          <option value="1">High</option>
          <option value="2">Medium</option>
          <option value="3">Low</option>
        </select>
        <select name="date" onChange={_ => false}>
          {this.dateInArr()}
        </select>
        <textarea name="description" placeholder="Description">
        </textarea>
        <input type="submit" value="Add" onClick={this.click} />
      </form>
    );
  }
}



