import React, {Component} from 'react';
import {getDate} from '../handleDate';

export class Table extends Component {
  constructor (props) {
    super();
    this.arr = [`Done`, `Title`, `Priority`, `Date`];
  };
  clickThead = (e) => {
    if (e.target.matches(`.up`)) {
      this.sort(e.target.closest(`th`).id.toLowerCase(), `up`);
    };
    if (e.target.matches(`.down`)) {
      this.sort(e.target.closest(`th`).id.toLowerCase(), `down`);
    };
  };
  sort (name, dir) {
    let data = JSON.parse(JSON.stringify(this.props.data));
    if (name !== `date`) {
      data.sort((a, b) => {
        let res = a[name] <= b[name] ? 1 : -1;
        if (dir === `up`) res *= -1;
        return res;
      });
    } else {
      data.sort((a, b) => {
        let res = getDate(a[name]) >= getDate(b[name]) ? 1 : -1;
        if (dir === `up`) res *= -1;
        return res;
      });
    }
    this.props.refresh(data);
  }
  showDescription (elem, key) {
    let coords = elem.getBoundingClientRect();
    let description;
    this.props.data.forEach(obj => {
      if (obj.key === key) description = obj.description;
    });
    this.description = <div id="description" style={{top: `${coords.bottom}px`, left: `${coords.left + 1}px`}}>{description}</div>
    this.props.refresh(this.props.data);
  }
  hideDescription = (e) => {
    this.description = false;
    this.props.refresh(this.props.data);
  }
  createHead () {
    let arr = [];
    this.arr.forEach(name => {
      arr.push(<th id={name} key={name}>
        <div>
          <div>{name}</div>
          <div>
            <button className={`up`}>&#9650;</button>
            <button className={`down`}>&#9660;</button>
          </div>
        </div>
      </th>);
    });
    return arr;
  };
  createBody () {
    let arr = [];
    this.props.data.forEach((obj) => {
      if ((!this.props.completed && !obj.done) || this.props.completed) {
        arr.push(<tr key={obj.key}>
          <td><input className="done" type="checkbox" defaultChecked={obj.done} onChange={_ => { this.props.changeDone(obj.key) }}/></td>
          <td onMouseOver={e => this.showDescription(e.target, obj.key)}
              onMouseOut={this.hideDescription}>
              {obj.title}
          </td>
          <td>{(() => {
            if (obj.priority === `1`) return `Hight`;
            if (obj.priority === `2`) return `Medium`;
            if (obj.priority === `3`) return `Low`;
          })()}</td>
          <td>{obj.date}</td>
        </tr>);
      }
    });
    return arr;
  };
  render () {
    return [
      <table key="table">
        <thead onClick={this.clickThead}>
          <tr>
            {this.createHead()}
          </tr>
        </thead>
        <tbody>
          {this.createBody()}
        </tbody>
      </table>,
    <div key="description">{this.description || ``}</div>
    ];
  };
};



