import React, {Component} from 'react';
import {render} from 'react-dom';
import {NewTask} from './components/newTask';
import {Filter} from './components/filter';
import {Table} from './components/table';
import {Local} from './local';
import "./style.css";
import "./filter.css";
import "./table.css";

class App extends Component {
  constructor () {
    super();
    this.local = new Local();
    this.local.read(`data`).then(obj => {
      let data = obj === null ? [] : obj.data;
      this.setState({data: data || [], completed: false, oldData: data || []});
    });
  };
  changeCompleted (completed) {
    this.setState({completed: completed});
  }
  refreshTable (newData) {
    this.setState({data: newData});
  }
  addData (newObj) {
    let data = [...this.state.oldData, newObj]
    this.setState({data: data, oldData: data});
  };
  changeDone (key) {
    let arr = this.state.oldData.map(obj => {
      if (obj.key === key) obj.done = !obj.done;
      return obj;
    });
    this.setState({data: arr, oldData: arr});
  }
  componentDidUpdate () {
    this.local.write(`data`, {data: this.state.oldData});
  }
  render () {
    if (!this.state) { return (
      <div id="todo">
        <img src="https://cdn-images-1.medium.com/max/800/1*v7ppqczZDarOEUKCqpyJ0w.gif" alt={`https://cdn-images-1.medium.com/max/800/1*v7ppqczZDarOEUKCqpyJ0w.gif`} width="150" height="150" />
      </div>
    )} else {
      return (
        <div id="todo">
          <NewTask add={arg => this.addData(arg)} />
          <Filter changeCompleted={arg => this.changeCompleted(arg)} refresh={arg => this.refreshTable(arg)} data={this.state.data} oldData={this.state.oldData}/>
          <Table completed={this.state.completed} refresh={arg => this.refreshTable(arg)} data={this.state.data} changeDone={arg => this.changeDone(arg)} />
        </div>
      );
    }
  }
}

render(<App />, document.querySelector('#root'));



