import React, {Component} from 'react';
import {getDate, getStrFromDate, aDay} from '../handleDate';

export class Filter extends Component {
  clickFilter = (e) => this.props.changeCompleted(e.target.checked);
  insertDates (from, to) {
    let arr = [];
    let dates = [];
    this.props.oldData.forEach(obj => arr.push(getDate(obj.date)));
    arr.sort((a, b) => a - b);
    from = from ? getDate(from) : arr[0];
    to = to ? getDate(to) : arr[arr.length - 1];
    for (let i = from; i <= to; i += aDay) {
      dates.push(<option key={i} value={getStrFromDate(new Date(i))}>{getStrFromDate(new Date(i))}</option>);
      if (i === arr[0]) this.defaultFrom = getStrFromDate(new Date(i));
      if (i === arr[arr.length - 1]) this.defaultTo = getStrFromDate(new Date(i));
    };
    return dates;
  }
  defineLimit (elem) {
    let limit;
    [...elem.options].forEach(option => {
      if (option.selected) limit = option.value;
    });
    this[elem.name] = limit;
  }
  startFilter = (e) => {
    let form = e.target.closest(`#filter`);
    let from, to;
    [...form.elements.from].forEach(option => {
      if (option.selected) from = getDate(option.value);
    });
    [...form.elements.to].forEach(option => {
      if (option.selected) to = getDate(option.value);
    });
    let input = form.elements.search.value.toLowerCase();
    let data = this.props.oldData
      .filter(obj => {
        if (getDate(obj.date) >= from && getDate(obj.date) <= to) return true;
      })
      .filter(obj => {
        if (input !== ``) {
          if (obj.title.toLowerCase().indexOf(input) >= 0 || obj.description.toLowerCase().indexOf(input) >= 0) return true;
        } else {
          return true
        }
      });
      this.props.refresh(data);
  };
  render () {
    this.insertDates();
    const from = this.from || this.defaultFrom;
    const to = this.to || this.defaultTo;
    return (
      <form name="filter" id="filter">
        <input id="completed" name="completed" type="checkbox" onClick={this.clickFilter} /><label>Show completed</label>
        <select value={from} name="from" onChange={e => { this.defineLimit(e.target); this.startFilter(e); }}>
          {this.insertDates(undefined, this.to)}
        </select>
        <select value={to} name="to" onChange={e => { this.defineLimit(e.target); this.startFilter(e); }}>
          {this.insertDates(this.from)}
        </select>
        <input name="search" type="text" placeholder="Search" onInput={this.startFilter} />
      </form>
    );
  };
};



